import React from "react";
import { connect } from "react-redux";
import UserSearchPage from "./UserSearchPage";
import {
    setCurrentPage,
    receiveUsers,
    follow,
    unfollow,
} from "./../../Redux/userSearch-reducer";
import { getCurrentPage, getCountSize, getIsLoaded, getIsFollowingInProgress, getUsersSelector, getPages } from "./../../Redux/selectors/searchUsers";


class UserSearchPageContainer extends React.Component {
    componentDidMount() {
        this.props.receiveUsers(this.props.currentPage, this.props.countSize);
    };

    onPageChanged = (pageNumber) => {
        this.props.receiveUsers(pageNumber, this.props.countSize);
        this.props.setCurrentPage(pageNumber);
    };

    render() {
        return (
            <UserSearchPage
                currentPage={this.props.currentPage}
                users={this.props.users}
                onPageChanged={this.onPageChanged}
                isLoaded={this.props.isLoaded}
                isFollowingInProgress={this.props.isFollowingInProgress}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                pages={this.props.pages}
            />
        );
    };
};

let stateToProps = (state) => {
    return {
        users: getUsersSelector(state),
        currentPage: getCurrentPage(state),
        countSize: getCountSize(state),
        isLoaded: getIsLoaded(state),
        isFollowingInProgress: getIsFollowingInProgress(state),
        pages: getPages(state),
    };
};

export default connect(stateToProps, {
    setCurrentPage,
    receiveUsers,
    follow,
    unfollow
})(UserSearchPageContainer);
