import { useState } from "react";
import style from "./Paginator.module.css";

const Paginator = ({ pages, onPageChanged, currentPage, portionSize }) => {
    let portionCount = Math.ceil(pages.length / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);

    const initialNumberPagePortion = portionSize * (portionNumber - 1);
    const terminalNumberPagePortion = portionSize * portionNumber;

    const incrementPortionNumber = () => {
        if (portionNumber !== portionCount) {
            setPortionNumber(portionNumber + 1);
        }
    };

    const decrementPortionNumber = () => {
        if (portionNumber!==1) {
            setPortionNumber(portionNumber - 1);
        }
    };

    return (
        <div className={style.paginator}>
            <button
                onClick={decrementPortionNumber}
                className={`${
                    portionNumber === 1 ? style.disabled : style.navigation
                }`}
            >
                {"<< prew"}
            </button>
            {pages
                .filter(
                    (e) =>
                        e > initialNumberPagePortion &&
                        e <= terminalNumberPagePortion
                )
                .map((el) => (
                    <div key={el} className={style.button}>
                        <button
                            onClick={() => {
                                onPageChanged(el);
                            }}
                            className={
                                el === "..."
                                    ? style.disabled
                                    : currentPage === el
                                    ? style.page + " " + style.activePage
                                    : style.page
                            }
                            disabled={el === "..."}
                        >
                            {el}
                        </button>
                    </div>
                ))}
            <button
                onClick={incrementPortionNumber}
                className={`${
                    portionNumber === portionCount
                        ? style.disabled
                        : style.navigation
                }`}
            >
                {"next >>"}
            </button>
        </div>
    );
};

export default Paginator;
