import style from "./ButtonDisabled.module.css";
import React from "react";

const ButtonDisabled = () => {
    return (
        <button
            disabled="true"
            className={style.disabled}
        >
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </button>
    );
};

export default ButtonDisabled;
